section.success {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
section.success .success-wrap {
  text-align: center;
  max-width: 370px;
}
section.success .title {
  font-size: 24px;
  font-weight: 700;
}
section.success .success-subtitle {
  font-size: 18px;
}
section.success .brief-link {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 246px;
  margin: 0 auto 25px;
  min-height: 50px;
  background-color: #1769FF;
  text-decoration: none;
  font-size: 14px;
  font-weight: 700;
}
section.success .top-message {
  padding-bottom: 30px;
  margin-bottom: 30px;
  position: relative;
}
section.success .top-message::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: calc(50% - 120px);
  width: 240px;
  height: 2px;
  background: linear-gradient(90deg, rgba(196, 196, 196, 0) 0%, #FFFFFF 49.48%, rgba(196, 196, 196, 0) 100%);
}
section.success .top-message .title {
  margin-bottom: 15px;
}
section.success .bottom-message .title {
  margin-bottom: 25px;
}
section.success span {
  display: block;
}

@media (max-width: 575px) {
  section.success .success-wrap {
    max-width: 250px;
  }
  section.success .title {
    font-size: 18px;
  }
  section.success .success-subtitle {
    font-size: 14px;
    padding: 0 30px;
  }

  main .button-back {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 5;
  }
}